@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,300&family=Roboto:ital,wght@0,300;0,500);
html {
	overflow-y: auto;
}

body {
	--primary-bg-color: rgb(37, 33, 33);
	--secondary-bg-color: rgb(219, 219, 219);
	--tertiary-bg-color: rgb(66, 60, 53);
	--quaternary-bg-color: rgb(58, 51, 44);

	--btmPrimary-bg-color: rgb(26, 23, 23);
	--btmSecondary-bg-color: rgb(44, 44, 44);
	--btmTertiary-bg-color: rgb(66, 60, 53);

	--cardPrimary-bg-color: rgb(54, 51, 51);

	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI',
		'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-color: var(--tertiary-bg-color);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas,
		'Courier New', monospace;
}

.Header {
	position: relative;
	background-color: var(--primary-bg-color);
	z-index: 10;
	height: 30px;
	display: flex;
	justify-content: space-between;
}

h1 {
	font-size: 30px;
	font-family: 'Roboto';
	color: var(--secondary-bg-color);
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	position: relative;
	background-color: var(--primary-bg-color);
	text-shadow: 0.5px 0.5px 1px rgb(110, 110, 110),
		1px 1px 1px rgb(94, 94, 94), 2px 2px 1px rgb(82, 82, 82),
		3px 3px 0.5px rgb(70, 70, 70),
		4px 4px 0.5px rgb(48, 48, 48);
}

.NavBar {
	display: flex;
	flex-direction: column;
	background-color: var(--tertiary-bg-color);
	margin-right: 10px;
	margin-right: 10px;
	position: relative;
	z-index: 10;
	box-shadow: 0px 5px 10px;
	width: 100%;
	height: 100%;
	padding: 5px;
}

.navBarList {
	display: flex;
	flex-direction: column !important;
	padding: 0px !important;
	background-color: var(--quaternary-bg-color);
}

.NavBar a {
	font-family: Roboto;
	font-size: 18px;
	color: var(--secondary-bg-color);
	text-decoration: none;
	padding: 5px;
}

.NavBar a:hover {
	background-color: var(--btmSecondary-bg-color);
}

.icon {
	position: relative;
	top: 5px;
	padding-right: 5px;
}

.App {
	font-family: Roboto;
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

button {
	border: 2px solid var(--btmPrimary-bg-color);
	height: 30px;
	border: 0px;
	background-color: var(--btmPrimary-bg-color);
	color: var(--secondary-bg-color);
	border-radius: 5px;
	cursor: pointer;
	transition: 0.2s;
}

button:hover:hover {
	background-color: var(--btmSecondary-bg-color);
}

.card {
	/* border: 3px solid var(--primary-bg-color); */
	border-radius: 5px;
	padding: 10px;
	margin: 10px;
	box-shadow: 0px 3px 5px;
	background-color: var(--cardPrimary-bg-color);
}

form {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

p,
h2,
h3,
h4,
h5 {
	color: var(--secondary-bg-color);
}

.post {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.fitText {
	width: 50vw;
	max-width: 700px;
	word-wrap: break-word;
}

hr {
	width: 100%;
	border: 1px solid black;
}

.Post {
	width: 100vw;
	max-width: 900px;
	text-align: start;
}

.center {
	margin-left: 10px;
	text-align: center;
}

a {
	font-size: 20px;
	color: var(--secondary-bg-color);
	text-decoration: none;
}

a:hover {
	color: var(--secondary-bg-color);
}

@-webkit-keyframes rotation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.img {
	height: 100px;
	width: 100px;
}

.Loading {
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-animation: rotation 1s infinite linear;
	        animation: rotation 1s infinite linear;
	height: 100px;
	width: 100px;
}

.Login {
	display: flex;
	justify-content: center;
}

.Register {
	display: flex;
	justify-content: center;
}

.Profile {
	display: flex;
	justify-content: center;
}

.PostForm {
	display: flex;
	justify-content: center;
}

textarea,
input {
	background-color: var(--secondary-bg-color);
	max-width: 100vw;
}

label {
	color: white;
	padding-bottom: 5px;
	padding-top: 5px;
}

.tagList {
	border: 1px solid black;
	width: 100px;
	min-height: 20px;
	background-color: var(--quaternary-bg-color);
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

hr {
	border: 1px solid var(--primary-bg-color);
}

.PostView {
	display: flex;
	justify-content: center;
}

.fullPost {
	width: 100vw;
	max-width: 1000px;
}

.author {
	height: 100px;
	width: 200px;
}
.lowerPost {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-top: 3px solid var(--primary-bg-color);
}

.postContent {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.tags {
	margin: 5px;
	border: 3px solid var(--primary-bg-color);
	border-radius: 5px;
}

.tag {
	margin: 5px;
	border-top: 1px solid var(--primary-bg-color);
	border-bottom: 1px solid var(--primary-bg-color);
	font-size: 12px;
}

p {
	color: var(--secondary-bg-color);
}

.Vote {
	display: flex;
	flex-direction: column;
	justify-content: center;
	border: 3px solid var(--primary-bg-color);
	background-color: var(--tertiary-bg-color);
	border-radius: 5px;
	padding: 5px;
}

.commentEditor {
	width: 80%;
	height: 100px;
	resize: none;
	margin-right: 10px;
}

.commentForm {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
}

